  mounted() {
    const that = this;

    this.window = window;

    $.getJSON("./assets/json/data.json", function(JSON_DATA) {
      that.json_data = JSON_DATA;

      if (that.json_data.title) {
        that.title = that.json_data.title;
      }

      if (that.json_data.social) {
        that.social = that.json_data.social;
      }

      that.json_data.stock.forEach(element => {
        const item = {};
        that.dessertHeaders.forEach(column => {
          if (
            (column.value == "last_buy_date" ||
              column.value == "stockholder_date" ||
              column.value == "vote_start_date" ||
              column.value == "vote_end_date" ||
              column.value == "pick_start_date" ||
              column.value == "pick_end_date") &&
            element[column.value] != ""
          ) {
            item[column.value] = that.getDate(element[column.value]);
          } else {
            item[column.value] = element[column.value];
          }
        });
        that.desserts.push(item);
      });
    });
  },
  methods: {
    getUrl(url) {
      return "https://" + url;
    },
    getDate(date) {
      return moment(date).format("M月DD日");
    }
  }
